	module.exports = {"data":{"markdownRemark":{"html":"<p>I løpet av oppholdet i Roma vinteren 1869-70 gjorde Grieg en del skisser, og kanskje var utkastet til opus 19 blant disse. Verket ble ferdig på Landås sommeren 1871, og Grieg fremførte det ofte på sine konserter. Nr. 2 i samlingen, Brudefølget drar forbi, er et av Griegs skattede pianostykker, og er også utgitt i firhendig versjon.</p>\n<p>A-mollkonserten ble etterfulgt av den store samlingen av 25 norske folkeviser og danser, opus 17. Men arbeidet med Lindemans innsamlede folkemelodier stoppet ikke her. Neste opus med stoff fra Lindeman var Folkelivsbilder, opus 19.\nGrieg skriver i forordet til Folkelivsbildene:</p>\n<blockquote>\n<p>«Det bemerkes at kjennskapet til de to første stykkene er nødvendig for forståelsen av det siste. I karnevalet skimter man nemlig blant den brokede vrimmel i det fjerne et norsk brudefølge, som avløses av kjempeskikkelser, som i store hallingkast (motiver fra Fjellslåtten) liksom rydder skueplassen. Til slutt kommer det flyvende ritt, som antydet ved den etter ”Stretto”-inntredende kvint i A-dur: situasjonen er på dette punkt av karnevalet utartet til fullkommen villhet. Mengdens piping, skriking, dertil hestene, som fnysende som i et eneste drag skjærer luften, alt fortoner seg til et bilde av den mest overgivne kåthet.»</p>\n</blockquote>\n<p>Disse ”programmatiske” kommentarer synes kanskje overflødige og til dels ”ledende”, for musikken taler sitt umiskjennelige språk i dette livlige møte mellom den nordiske kraft og klang og den latinske varme, sødme og lettlivethet. Grieg opplevde karnevalstiden i Roma, og han ble bokstavelig talt revet med av folkelivet og de skjønne romerske kvinners forførende lekenhet.</p>","timeToRead":1,"excerpt":"I løpet av oppholdet i Roma vinteren 1869-70 gjorde Grieg en del skisser, og kanskje var utkastet til opus 19 blant disse. Verket ble ferdig…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus19.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Folkelivsbilder","composedin":"1871","startDate":"15.6","startTime":null,"location":"Troldhaugen","artists":["Jorunn Marie Bratlie"],"medialink":null,"title":"19","dedicatedTo":"J. P. E. Hartmann","author":"Erling Dahl jr.","date":"2018-05-07T12:00:00.048Z","category":"opus","tags":["klaver"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.7359855334538878,"src":"/static/opus19-7ed7352d551efc3fd3bfc24b0ded9949-47774.jpg","srcSet":"/static/opus19-7ed7352d551efc3fd3bfc24b0ded9949-45ad6.jpg 480w,\n/static/opus19-7ed7352d551efc3fd3bfc24b0ded9949-e2fcf.jpg 960w,\n/static/opus19-7ed7352d551efc3fd3bfc24b0ded9949-47774.jpg 1920w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/opus19/","artist":[{"frontmatter":{"title":"Jorunn Marie Bratlie","category":"artister","tags":["pianist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-466fa.jpg","srcSet":"/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-27b23.jpg 90w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-0afbd.jpg 180w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-466fa.jpg 360w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-43088.jpg 540w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-545bd.jpg 720w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-8c1c4.jpg 1080w,\n/static/JorunnMarieBratlie-5fa81f3acb54b3e59875d9aa777a6ae5-63573.jpg 1776w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/jorunn-marie-bratlie/","opuses":[{"frontmatter":{"title":"14"}},{"frontmatter":{"title":"19"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus19/"}}

